<template>
  <section class="kits">
    <!-- End Header -->
    <!-- kite serving -->
    <div class="kite-serving">
      <div class="image" :class="scrollServing ? 'fadeInRight' : ''">
        <img src="../assets/1.svg" alt="kite Serving" />
      </div>

      <div class="info-text" :class="scrollServing ? 'fadeInLeft' : ''">
        <h2>Kite Serving</h2>
        <p>
          when an unknown printer took a galley of type and scrambled it to make
          a type specimen book. It has survived not only five centuries, but
          also the leap into electronic typesetting, remaining essentially
          unchanged.
        </p>
        <router-link class="btn" to="/contact-us">
          {{ $t("buttons.becomeSurfer") }}
        </router-link>
      </div>
    </div>
    <!-- kite serving -->

    <!-- kite sailor -->
    <div class="kite-sailor">
      <div class="image" :class="scrollSailor ? 'fadeInLeft' : ''">
        <img src="../assets/2.svg" alt="kite Serving" />
      </div>
      <div class="info-text" :class="scrollSailor ? 'fadeInRight' : ''">
        <h2>Kite Serving</h2>
        <p>
          when an unknown printer took a galley of type and scrambled it to make
          a type specimen book. It has survived not only five centuries, but
          also the leap into electronic typesetting, remaining essentially
          unchanged.
        </p>
        <router-link class="btn" to="/contact-us">
          {{ $t("buttons.becomeSailor") }}
        </router-link>
      </div>
    </div>
    <!-- kite sailor -->
    <div class="about-us" id="about-us">
      <div class="image" :class="scrollAboutUs ? 'fadeInRight' : ''">
        <img src="../assets/3.svg" alt="kite Serving" />
      </div>
      <div
        class="info-text"
        :class="[
          scrollAboutUs ? 'fadeInLeft ' : '',
          this.$i18n.locale === 'en' ? 'text-left' : 'text-right',
        ]"
      >
        <h2>{{ $t("misc.aboutFederation") }}</h2>
        <p>
          {{ $t("misc.aboutFederationDescription") }}
        </p>
        <router-link class="btn" to="/about">
          {{ $t("buttons.readMore") }}
        </router-link>
      </div>
    </div>
  </section>
</template>
<script>
export default {
  data() {
    return {
      scrollServing: false,
      scrollSailor: false,
      scrollAboutUs: false,
    };
  },
  created() {
    window.addEventListener("scroll", this.checkScroll);
  },
  methods: {
    checkScroll() {
      let kiteServing = document.querySelector(".kite-serving");
      let kiteSailor = document.querySelector(".kite-sailor");
      let aboutUs = document.querySelector(".about-us");
      if (window.scrollY >= kiteServing.offsetTop - 600) {
        this.scrollServing = true;
      } else {
        this.scrollServing = false;
      }
      if (window.scrollY >= kiteSailor.offsetTop - 600) {
        this.scrollSailor = true;
      } else {
        this.scrollSailor = false;
      }
      if (window.scrollY >= aboutUs.offsetTop - 600) {
        this.scrollAboutUs = true;
      } else {
        this.scrollAboutUs = false;
      }
    },
  },
};
</script>
